import { Navigate, Outlet, useLocation } from "react-router-dom";
import { useContext } from "react";
import Context from "./context/UserContext";

const PrivateRoute = () => {
  const { user } = useContext(Context);
  const location = useLocation();

  // Rutas públicas excepcionales
  const publicExceptions = ["/seeWebinar"];

  // Verifica si la ruta actual comienza con alguna excepción
  const isPublicException = publicExceptions.some((path) =>
    location.pathname.startsWith(path)
  );

  return user || isPublicException ? <Outlet /> : <Navigate to="/loginuser" />;
};

export default PrivateRoute;
